import { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import Cookies from "universal-cookie";
import PagarAppService from "../../Service/PagarAppService";
import TiendaService from "../../Service/TiendaService";
import KeycloakService from "../../Service/KeycloakService";
import QRCode from "react-qr-code";
import QrEnzonaService from "../../Service/QrEnzonaService";

export const CheckoutComponent = () => {
  const [idusuario, setIdusuario] = useState("aa");
  const [nombreusuario, setNombreusuario] = useState("aa");
  const [pagado, setPagado] = useState(true);
  const [reference, setReference] = useState("Tienda EnTuMovil");
  const [concept, setConcept] = useState("aaa");
  const [description, setDescription] = useState("aaaa");
  const [amount, setAmount] = useState("152");
  const [currency, setCurrency] = useState("USD");
  const [expirationDays, setExpirationDays] = useState(1);
  const [runway, setRunway] = useState(2);
  const [confirmationRute, setConfirmationRute] = useState(0);
  const [merchant, setMerchant] = useState("entumovil");
  const [successUrl, setSuccessUrl] = useState("http://localhost:2999/ok");
  const [urlretorno, setUrlretorno] = useState("http://localhost:2999/");
  const [errorUrl, setErrorUrl] = useState("http://localhost:2999/error");

  const navigate = useNavigate();
  const { id } = useParams();

  let kc = KeycloakService.getKc();

  const paymentIntent = (e) => {
    e.preventDefault();

    const payment = {
      idusuario,
      nombreusuario,
      pagado,
      reference,
      concept,
      description,
      amount,
      currency,
      expirationDays,
      runway,
      confirmationRute,
      merchant,
      successUrl,
      urlretorno,
      errorUrl,
    };

    const cookies = new Cookies();
    const accessToken = cookies.get("token");
    PagarAppService.newPaymentIntent(payment, accessToken)
      .then((response) => {
        console.log(response.data);
        window.open(response.data.shortUrl, "_blank");

        navigate("/");
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    const cookies = new Cookies();
    const accessToken = cookies.get("token");
    TiendaService.getAppById(id, accessToken)
      .then((response) => {
        setIdusuario(kc.tokenParsed.preferred_username);
        setNombreusuario(kc.tokenParsed.name);
        setConcept(response.data.nombre);
        setDescription(
          response.data.descripcion + " " + response.data.tamanno + "GB"
        );

        setAmount(response.data.precio);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [id]);

  const title = () => {
    return <h2 className="text-center">Detalles del pago</h2>;
    //<h2 className="text-center">{title()}</h2>;
  };

  return (
    <div>
      <div className="container">
        <div className="gaming-library">
          <div className="col-lg-12">
            <div>
              <div className="heading-section">
                <h4>
                  FORMULARIO DE <em>PAGO </em>
                </h4>
              </div>
              <div className="order-md-1">
                <form className="needs-validation" noValidate>
                  <div className="mb-3">
                    <h4>ID Usuario</h4>
                    <input
                      className="form-control"
                      type="text"
                      placeholder="id"
                      name="idusuario"
                      value={idusuario}
                      disabled="disable"
                      onChange={(e) => setIdusuario(e.target.value)}
                    />
                  </div>
                  <div className="mb-3">
                    <h4>Usuario</h4>
                    <div className="input-group">
                      <div className="input-group-prepend">
                        <span className="input-group-text">@</span>
                      </div>

                      <input
                        className="form-control"
                        type="text"
                        placeholder="Usuario"
                        name="nombreusuario"
                        value={nombreusuario}
                        disabled="disable"
                        onChange={(e) => setNombreusuario(e.target.value)}
                      />
                      <div className="invalid-feedback">
                        Su nombre de usuario es requerido.
                      </div>
                    </div>
                  </div>

                  <div className="mb-3">
                    <h4>Articulo</h4>
                    <input
                      className="form-control"
                      type="text"
                      placeholder="Nombre del articulo"
                      name="concept"
                      value={concept}
                      disabled="disable"
                      onChange={(e) => setConcept(e.target.value)}
                    />
                  </div>
                  <div className="mb-3">
                    <h4>
                      Descripcion<span className="text-muted">(Opcional)</span>
                    </h4>

                    <input
                      className="form-control"
                      type="text"
                      placeholder="Descripcin"
                      name="description"
                      value={description}
                      disabled="disable"
                      onChange={(e) => setDescription(e.target.value)}
                    />
                  </div>

                  <div className="mb-3">
                    <h4>Monto</h4>
                    <input
                      className="form-control"
                      type="text"
                      placeholder="0"
                      name="amount"
                      value={amount}
                      disabled="disable"
                      onChange={(e) => setAmount(e.target.value)}
                    />
                  </div>

                  <div className="mb-3">
                    <h4>Pais de origen</h4>
                    <select
                      className="custom-select d-block w-100"
                      id="pais"
                      required
                    >
                      <option value="">Cuba</option>
                      <option>Espaa</option>
                    </select>
                  </div>

                  <h4>Pago internacional</h4>
                  <button
                    className="btn btn-success"
                    onClick={(e) => paymentIntent(e)}
                  >
                    TROPIPAY
                  </button>
                  <hr className="mb-4" />
                  <div>
                    &nbsp;&nbsp;
                    <Link to="/" className="btn btn-danger ">
                      Cancelar
                    </Link>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CheckoutComponent;
