import axios from "axios";
import Keycloak from "keycloak-js";
import Cookies from "universal-cookie";
import { useEffect, useState } from "react";

let initOptions = {
  //url: "http://192.168.1.102:9090/",
  url: "http://localhost:9090/",
  realm: "entumovil-dev",
  clientId: "entumovil-service-frontend-ecommerce",
};

let kc = new Keycloak(initOptions);
let cookies = new Cookies();

class KeycloakService {
  loginOnKeycloak() {
    try {
      kc.init({
        onLoad: "login-required", //check-sso , login-required
        checkLoginIframe: false,
        pkceMethod: "S256",
      }).then(
        (auth) => {
          if (!auth) {
            window.location.reload();
          } else {
            console.info("Authenticated");
            console.log("auth", auth);
            console.log("Keycloak", kc);
            console.log("Access Token", kc.token);

            cookies.set("token", kc.token, { path: "/" });
            cookies.remove("name");

            cookies.set("name", kc.tokenParsed.name, { path: "/" });

            /* http client will use this header in every request it sends */
            // httpClient.defaults.headers.common[
            //   "Authorization"
            //] = `Bearer ${kc.token}`;

            kc.onTokenExpired = () => {
              console.log("token expired");
            };
          }
        },
        () => {
          console.error("Authenticated Failed");
        }
      );
    } catch (error) {
      console.error("Failed to login on keycloak: ", error);
    }

    return kc;
  }

  // Verificar kc
  getKc() {
    return kc;
  }

  // Obtener el token de acceso del usuario
  static getToken() {
    return kc.token;
  }

  // Renovar el token de acceso del usuario si est a punto de expirar
  static updateToken(minValidity) {
    return kc.updateToken(minValidity);
  }

  // Cerrar la sesin del usuario
  static logout() {
    return kc.logout();
  }

  // Obtener el perfil del usuario
  getProfile() {
    return kc.loadUserProfile();
  }

  // Obtener el nombre del usuario
  getUserName(nombreusuario) {
    return nombreusuario;
  }

  // Verificar si el usuario tiene un rol determinado
  static hasRole(role) {
    return kc.hasRealmRole(role);
  }

  // Verificar si el usuario est autenticado
  isAuthenticated() {
    return kc.authenticated;
  }
}

export default new KeycloakService();
