import axios from "axios";

const ENZONA_BASE_REST_API_URL = "http://localhost:8071/tienda/qr";

class QrEnzonaService {
  getQrById(QrId, accessToken) {
    return axios.get(ENZONA_BASE_REST_API_URL + "/" + QrId, {
      headers: {
        Authorization: "Bearer " + accessToken,
      },
    });
  }

  newQr(payment, accessToken) {
    return axios.post(ENZONA_BASE_REST_API_URL + "/account", payment, {
      headers: {
        Authorization: "Bearer " + accessToken,
      },
    });
  }

  getLogin(accessToken) {
    return axios.post("http://localhost:8071/tienda/generarToken", {
      headers: {
        Authorization: "Bearer " + accessToken,
      },
    });
  }
}

export default new QrEnzonaService();
