import axios from "axios";

const CHECKOUT_BASE_REST_API_URL = "http://localhost:8092/ecommerce";

class PagarService {
  //++++ +++ +++ ++ + Checkout + ++ +++ +++ ++++//

  newPayment(payment, accessToken) {
    return axios.post(CHECKOUT_BASE_REST_API_URL + "/savePay", payment, {
      headers: {
        Authorization: "Bearer " + accessToken,
      },
    });
  }
  newPaymentIntent(payment, accessToken) {
    return axios.post(CHECKOUT_BASE_REST_API_URL + "/newPay", payment, {
      headers: {
        Authorization: "Bearer " + accessToken,
      },
    });
  }

  getLogin() {
    return axios.get("http://localhost:8088/api/checkout/login");
  }
}

export default new PagarService();
