import React, { useEffect, useState } from "react";
import "bootstrap";

import QRCode from "react-qr-code";
import QrEnzonaService from "../../Service/QrEnzonaService";
import Cookies from "universal-cookie";

const QrCodeComponent = () => {
  const [idQr, setIdQr] = useState("0");

  const [amount, setAmount] = useState("0.00");
  const [currency, setCurrency] = useState("CUP");
  const [description, setDescription] = useState("Descripcin");
  QrEnzonaService.getLogin();

  const generarToken = (e) => {
    e.preventDefault();
    const payment = {
      amount,
      currency,
      description,
    };

    const cookies = new Cookies();
    const accessToken = cookies.get("token");
    QrEnzonaService.newQr(payment, accessToken)
      .then((response) => {
        console.log(response.data);
        setIdQr(response.data.vendor_identity_code);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const qrImage = () => {
    return (
      <div>
        <h2 className="text-center">Detalles del pago</h2>;
        <div class="text-center">
          <QRCode value={idQr} />
        </div>
      </div>
    );
  };

  useEffect(() => {
    qrImage();
  }, [idQr]);

  return (
         <div>
            <div class="text-center">
              <QRCode value={idQr} />
            </div>
          </div>

  );
};

export default QrCodeComponent;
