import "bootstrap";
import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import Cookies from "universal-cookie";
import TiendaService from "../../Service/TiendaService";

export const GamingLibraryComponent = () => {
  const [apps, setApps] = useState([]);
  const [appID, setAppID] = useState();
  const { control } = useParams();

  useEffect(() => {
    listarApps();
  }, []);

  const listarApps = () => {
    const cookies = new Cookies();
    const accessToken = cookies.get("token");
    TiendaService.getAllApp(accessToken)
      .then((response) => {
        setApps(response.data);
        console.log(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div className="container">
      <div className="gaming-library">
        <div className="col-lg-12">
          <div className="heading-section">
            <h4>
              APLICACIONES DE <em>PAGO </em>
            </h4>
          </div>

          {apps.map((app) => (
            <div className="item">
              <ul key={app.id}>
                <li>
                  <img src={app.img} alt="" className="templatemo-item" />
                </li>
                <li>
                  <span>name</span>
                  <h4>{app.nombre}</h4>
                </li>
                <li>
                  <span>Date Added</span>
                  <h4>24/08/2036</h4>
                </li>
                <li>
                  <span>Tamao</span>
                  <h4>{app.tamanno}Gb</h4>
                </li>
                <li>
                  <span>Precio</span>
                  <h4>${app.precio}</h4>
                </li>
                <li>
                  <div className="main-border-button border-active">
                    <Link className="btn btn-info" to={`/pagar/${app.id}`}>
                      Comprar{" "}
                    </Link>
                  </div>
                </li>
              </ul>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default GamingLibraryComponent;
