import "bootstrap";

import { useEffect, useState } from "react";
import KeycloakService from "../../Service/KeycloakService";

const HeaderComponent = () => {
  let kc = KeycloakService.getKc();

  const [infoMessage, setInfoMessage] = useState(kc.clientId);
  const [infoUser, setInfoUser] = useState("Usuario");

  const [username, setUsername] = useState();

  const loadData = async () => {
    let kc = KeycloakService.getKc();
    if (!kc) {
      await new Promise((resolve) => setTimeout(resolve, 1000));
      return await loadData(); // Llamada recursiva
    }
    await new Promise((resolve) => setTimeout(resolve, 2000)); // Simulacin de una espera de 2 segundos
    setUsername(JSON.stringify(kc.tokenParsed.name).replace(/"/g, ""));
  };

  useEffect(() => {
    loadData();
  }, []);

  useEffect(() => {
    setInfoUser(username);
  }, [username]);

  return (
    <header className="header-area header-sticky">
      <div className="grid"></div>
      <div className="container">
        <div className="row">
          <div className="col-12">
            <nav className="main-nav">
              <h3 className="active">BIENVENIDO A LA TIENDA DE APLICACIONES</h3>
              <a href="index.html" className="logo">
                <img src="assets/images/th.jpg" alt="" />
              </a>
              <ul className="nav">
                <li className="nav-item dropdown">
                  <a
                    className="nav-link dropdown-toggle"
                    data-toggle="dropdown"
                    role="button"
                    aria-expanded="false"
                    onClick={() => {
                      setInfoUser(kc.tokenParsed.name);
                    }}
                  >
                    <img src="assets/images/profile-header.jpg" /> {infoUser}
                  </a>
                  <div className="dropdown-menu dropdown-content">
                    <a onClick={kc.accountManagement} className="dropdown-item">
                      <i class=" nav-icon"></i>
                      <p>Perfil</p>
                    </a>
                    <a href="http://localhost:3001/" className="dropdown-item">
                      <i class=" nav-icon"></i>
                      <p>Gestionar Pagos</p>
                    </a>
                    <div className="dropdown-divider"></div>
                    <a onClick={kc.logout} className="dropdown-item">
                      Cerrar sesin
                    </a>
                  </div>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </header>
  );
};

export default HeaderComponent;
