import axios from "axios";

const ECOMMERCE_BASE_REST_API_URL = "http://localhost:8092/ecommerce";

class TiendaService {
  //++++ +++ +++ ++ + ECOMMERCE + ++ +++ +++ ++++//
  getAllApp(accessToken) {
    return axios.get(ECOMMERCE_BASE_REST_API_URL + "/all", {
      headers: {
        Authorization: "Bearer " + accessToken,
      },
    });
  }

  getAppById(appId, accessToken) {
    return axios.get(ECOMMERCE_BASE_REST_API_URL + "/" + appId, {
      headers: {
        Authorization: "Bearer " + accessToken,
      },
    });
  }

  updateApp(appId, app) {
    return axios.put(ECOMMERCE_BASE_REST_API_URL + "/" + appId, app);
  }

  deleteApp(appId) {
    return axios.delete(ECOMMERCE_BASE_REST_API_URL + "/" + appId);
  }

  newApp(appId, accessToken) {
    return axios.post(ECOMMERCE_BASE_REST_API_URL + "/save", appId, {
      headers: {
        Authorization: "Bearer " + accessToken,
      },
    });
  }
}

export default new TiendaService();
