import "./App.css";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import HeaderComponent from "./Components/Index/HeaderComponent";
import FooterComponent from "./Components/Index/FooterComponent";
import AppComponent from "./Components/Index/AppComponent";
import CheckoutComponent from "./Components/Index/CheckoutComponent";

import React from "react";
import KeycloakService from "./Service/KeycloakService";
import QrCodeComponent from "./Components/Index/QrCodeComponent";

function App() {
  KeycloakService.loginOnKeycloak();

  return (
    <div>
      <div>
        <HeaderComponent />
      </div>
      <div className="grid">
        <div className="col"></div>
      </div>
      <div className="App">
        <div>
          <BrowserRouter>
            <Routes>
              <Route path="/" element={<AppComponent />}></Route>
              <Route path="/qrCode" element={<QrCodeComponent />}></Route>
              <Route path="/index.html" element={<AppComponent />}></Route>
              <Route path="/pagar/:id" element={<CheckoutComponent />}></Route>
            </Routes>
          </BrowserRouter>
        </div>
        <FooterComponent />
      </div>
    </div>
  );
}

export default App;
